<template>
  <el-dialog
    title="编辑"
    :visible.sync="dialogTableVisible"
    :before-close="handleClose"
  >
    <el-form
      ref="form"
      :rules="rules"
      :model="site"
    >
      <el-form-item prop="name">
        <el-input
          placeholder="名称"
          v-model="site.name"
        ></el-input>
      </el-form-item>
      <el-form-item prop="url">
        <el-input
          placeholder="地址"
          v-model="site.url"
        ></el-input>
      </el-form-item>
      <el-form-item prop="iconUrl">
        <el-input
          placeholder="图标地址"
          v-model="site.iconUrl"
        ></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer">
      <el-button
        type="primary"
        @click="saveMark()"
      >确定</el-button>
      <el-button @click="cancelEdit()">取消</el-button>
    </div>
  </el-dialog>
</template>
<script>
export default {
  data () {
    return {
      dialogTableVisible: false,
      site: {},
      rules: {
        name: [
          { required: true, message: '请输入', trigger: 'blur' },
          { max: 1000, message: '请输入长度1至1000的内容', trigger: 'blur' }
        ],
        url: [
          { required: true, message: '请输入', trigger: 'blur' },
          { validator: this.urlValidate, trigger: 'blur' },
          { type: 'url', message: '请输入正确的url。例：http://www.aaa.com', trigger: 'blur' }
        ],
        iconUrl: [
          { max: 1000, message: '请输入长度1至1000的内容', trigger: 'blur' }
        ]
      },
      oldSite: {}
    }
  },
  props: {
    mode: { type: String, require: true }
  },
  methods: {
    showDialog () {
      this.dialogTableVisible = true
    },
    hideDialog () {
      this.$refs.form.clearValidate()
      this.site = {}
      this.dialogTableVisible = false
    },
    openDialog (index) {
      let markedSite = localStorage.getItem('markedSite')
      let markedSiteJson = JSON.parse(markedSite)
      this.site = markedSiteJson[index]
      this.oldSite = {
        name: this.site.name,
        url: this.site.url,
        iconUrl: this.site.iconUrl
      }
      this.showDialog()
    },
    saveMark () {
      let VueObj = this
      this.$refs.form.validate(valid => {
        if (valid) {
          let markedSite = localStorage.getItem('markedSite')
          let markedSiteJson = []
          if (markedSite) {
            markedSiteJson = JSON.parse(markedSite)
          }
          if (VueObj.mode === 'add') {
            markedSiteJson.push(VueObj.site)
          } else if (VueObj.mode === 'update') {
            markedSiteJson.forEach((item, index) => {
              if (item.url === VueObj.oldSite.url) {
                markedSiteJson[index] = VueObj.site
              }
            })
          }
          localStorage.setItem('markedSite', JSON.stringify(markedSiteJson))
          VueObj.hideDialog()
          VueObj.$emit('savedHandle')
        }
      })
    },
    cancelEdit () {
      this.hideDialog()
    },
    handleClose (done) {
      this.$refs.form.clearValidate()
      this.site = {}
      done()
    },
    urlValidate (rule, value, callback) {
      if (this.mode === 'add') {
        let markedSite = localStorage.getItem('markedSite')
        if (markedSite) {
          let markedSiteJson = JSON.parse(markedSite)
          if (markedSiteJson.length === 0) {
            callback()
          } else {
            let VueObj = this
            markedSiteJson.forEach(item => {
              if (item.url === VueObj.site.url) {
                callback(new Error('已存在'))
              } else {
                callback()
              }
            })
          }
        } else {
          callback()
        }
      } else {
        callback()
      }
    }
  }
}
</script>



