<template>
  <el-input
    placeholder="请输入内容"
    v-model="searchParam.content"
    class="search-input"
    size="medium"
  >
    <el-select
      v-model="searchParam.service"
      slot="prepend"
      class="search-service"
    >
      <el-option
        v-for="(item,index) in searchService"
        :label="item.name"
        :value="item.code"
        :key="index"
      ></el-option>
    </el-select>
    <el-button
      slot="append"
      icon="el-icon-search"
      @click="search()"
    ></el-button>
  </el-input>
</template>
<script>
import { querySearchService } from './../assets/api/index'
export default {
  data () {
    return {
      searchService: [],
      searchParam: { service: '' }
    }
  },
  created () { this.getSearchService() },
  methods: {
    getSearchService () {
      querySearchService().then(data => {
        this.searchService = data
        this.searchParam.service = data[0].code
      })
    },
    search () {
      let VueObj = this
      let url = this.searchService.filter(item => {
        return VueObj.searchParam.service === item.code
      })[0].url
      url += this.makeParam()
      window.open(url)
    },
    makeParam () {
      switch (this.searchParam.service) {
        case 'baidu': return '?wd=' + this.searchParam.content
        case 'google': return '?q=' + this.searchParam.content
        case 'bing': return '?q=' + this.searchParam.content
      }
    }
  }
}
</script>
<style scoped>
.search-input {
  margin-left: 20%;
  width: 60%;
  margin-right: 20%;
}
.search-service {
  width: 100px;
}
</style>



