import Vue from 'vue'
import VueI18n from 'vue-i18n'
import zhCNLocale from './i18n_zh-CN'
import enUSLocale from './i18n_en-US'
import jaJPLocale from './i18n_ja-JP'

Vue.use(VueI18n)

const messages = {
  'zh-CN': zhCNLocale,
  'en-US': enUSLocale,
  'ja-JP': jaJPLocale
}
var lang = localStorage.getItem('locale')
if (lang === undefined || lang === null) {
  lang = 'zh-CN'
}
const i18n = new VueI18n({
  locale: lang,
  messages
})

export default i18n



