<template>
  <div>
    <div style="min-height:170px">
      <div><label>常用</label></div>
      <div>
        <el-card
          v-for="item in markedSite"
          :key="item.name"
          shadow="hover"
          class="markedSite-container"
        >
          <a
            :href="item.url"
            target="_blank"
          >
            <div class="markedSite-ico">
              <img
                :src="item.iconUrl===undefined?'./static/img/icon/link.png':item.iconUrl"
                class="markedSite-ico"
                onerror="this.src='./static/img/icon/link.png'"
              />
            </div>
            <div
              :title="item.name"
              class="markedSite-name"
            >{{item.name}}</div>
          </a>
        </el-card>
      </div>
    </div>
    <div>
      <div>
        <label>本地</label>
        <el-dropdown @command="handleCommand">
          <span>
            <el-button
              circle
              class="el-icon-more"
            ></el-button>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="upload">导入</el-dropdown-item>
            <el-dropdown-item command="download">导出</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div>
        <el-card
          v-for="(item,index) in localMarkedSite"
          :key="index"
          shadow="hover"
          class="markedSite-container"
        >
          <div class="markedSite-options">
            <el-dropdown @command="(command)=>{handleCommand(command,index)}">
              <span>
                <el-button
                  circle
                  class="el-icon-more"
                ></el-button>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="edit">编辑</el-dropdown-item>
                <el-dropdown-item command="delete">删除</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
          <a
            :href="item.url"
            target="_blank"
          >
            <div class="markedSite-ico">
              <img
                :src="item.iconUrl"
                class="markedSite-ico"
                onerror="this.src='./static/img/icon/link.png'"
              />
            </div>
            <div
              :title="item.name"
              class="markedSite-name"
            >{{item.name}}</div>
          </a>
        </el-card>
        <el-card
          shadow="hover"
          class="markedSite-container"
        >
          <div
            class="markedSite-ico el-icon-plus"
            style="font-size:50px;"
            @click="addSite()"
          ></div>
          <div
            class="markedSite-name"
            @click="addSite()"
          >添加</div>
        </el-card>
      </div>
    </div>
    <add-or-edit
      :mode="editMode"
      ref="addOrEdit"
      @savedHandle="savedHandle()"
    ></add-or-edit>
    <input
      type="file"
      accept="application/json"
      @change="fileChange()"
      style="display:none;"
      ref="fileUpload"
    >
  </div>
</template>
<script>
import { queryMarkedSite } from './../assets/api/index'
import AddOrEdit from './AddOrEdit'
export default {
  data () {
    return {
      markedSite: [],
      localMarkedSite: [],
      editMode: ''
    }
  },
  components: { AddOrEdit },
  created () { this.getMarkedSite() },
  methods: {
    getMarkedSite () {
      queryMarkedSite().then(data => {
        this.markedSite = data
        this.getlocalMarkedSite()
      })
    },
    getlocalMarkedSite () {
      let localMarkedSiteStr = localStorage.getItem('markedSite')
      if (localMarkedSiteStr) {
        this.localMarkedSite = JSON.parse(localMarkedSiteStr)
      }
    },
    addSite () {
      this.editMode = 'add'
      this.$refs.addOrEdit.showDialog()
    },
    deleteMark (index) {
      this.$confirm('确认删除吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.localMarkedSite.splice(index, 1)
        localStorage.setItem('markedSite', JSON.stringify(this.localMarkedSite))
        this.getlocalMarkedSite()
      })
    },
    editMark (index) {
      this.editMode = 'update'
      this.$refs.addOrEdit.openDialog(index)
    },
    savedHandle () {
      this.getlocalMarkedSite()
    },
    handleCommand (command, index) {
      switch (command) {
        case 'upload': this.upload(); break
        case 'download': this.downloadMark(); break
        case 'edit': this.editMark(index); break
        case 'delete': this.deleteMark(index); break
      }
    },
    downloadMark () {
      let localMark = localStorage.getItem('markedSite')
      const blob = new Blob([localMark])
      const aLink = document.createElement('a')
      let url = ''
      if (typeof blob === 'object' && blob instanceof Blob) {
        url = URL.createObjectURL(blob) //
      }
      aLink.href = url
      aLink.download = 'marks.json'
      document.body.appendChild(aLink)
      aLink.click()
      document.body.removeChild(aLink)
    },
    upload () {
      this.$refs.fileUpload.click()
    },
    fileChange () {
      var reader = new FileReader()
      reader.readAsText(this.$refs.fileUpload.files[0], 'UTF-8')
      let VueObj = this
      reader.onload = function (evt) {
        var fileString = evt.target.result
        let localMarkedSiteStr = localStorage.getItem('markedSite')
        let localMarkedSite = []
        if (localMarkedSiteStr) {
          localMarkedSite = JSON.parse(localMarkedSiteStr)
        }
        if (localMarkedSite.length === 0) {
          localMarkedSite.push.apply(localMarkedSite, JSON.parse(fileString))
        } else {
          let importMark = []
          try {
            importMark = JSON.parse(fileString)
            if (!(importMark instanceof Array)) {
              VueObj.$message({
                type: 'warning',
                message: '文件格式错误'
              })
            }
          } catch (error) {
            VueObj.$message({
              type: 'warning',
              message: '文件格式错误'
            })
          }
          let newMarkedSite = []
          let replaceMarkSite = []
          for (let itemIndex in importMark) {
            let filteredArr = localMarkedSite.filter(item => { return item.url === importMark[itemIndex].url })
            if (filteredArr.length === 0) newMarkedSite.push(importMark[itemIndex]); else replaceMarkSite.push(importMark[itemIndex])
          }
          for (let localIndex in localMarkedSite) {
            let filteredArr = replaceMarkSite.filter(item => { return item.url === localMarkedSite[localIndex].url })
            if (filteredArr.length !== 0) localMarkedSite.splice(localIndex, 1, filteredArr[0])
          }
          localMarkedSite.push.apply(localMarkedSite, newMarkedSite)
        }
        localStorage.setItem('markedSite', JSON.stringify(localMarkedSite))
        VueObj.getlocalMarkedSite()
      }
    }
  }
}
</script>
<style scoped>
.markedSite-container {
  width: 104px;
  height: 120px;
  margin: 10px;
  text-align: center;
  position: relative;
  overflow: visible;
  display: inline-block;
}
.markedSite-container:hover .markedSite-options {
  display: block;
}
.markedSite-ico {
  width: 64px;
  height: 64px;
}
.markedSite-name {
  width: 64px;
  word-break: keep-all;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.markedSite-options {
  display: none;
  position: absolute;
  top: -16px;
  right: -16px;
}
.el-button + .el-button {
  margin-left: 3px;
}
</style>



