import axios from 'axios'
export function queryMarkedSite () {
  return new Promise((resolve, reject) => {
    axios.get('./static/data/markedSite.json').then(res => {
      resolve(res.data)
    })
  })
}
export function querySearchService () {
  return new Promise((resolve, reject) => {
    axios.get('./static/data/searchService.json').then(res => {
      resolve(res.data)
    })
  })
}



